export const getNumberMonth = () => {
  let date = new Date();
  date.setDate(date.getDate() - 1);
  return () => {
    date.setDate(date.getDate() + 1);
    const dayNumber = date.getDate();
    const monthNumber = date.getMonth();
    const dayCount = date.getDay();
    let dayName = null;
    let monthName = null;
    switch (monthNumber) {
      case 0: {
        monthName = "—è–Ω–≤–∞—Ä—å";
        break;
      }
      case 1: {
        monthName = "—Ñ–µ–≤—Ä–∞–ª—å";
        break;
      }
      case 2: {
        monthName = "–º–∞—Ä—Ç";
        break;
      }
      case 3: {
        monthName = "–∞–ø—Ä–µ–ª—å";
        break;
      }
      case 4: {
        monthName = "–º–∞–π";
        break;
      }
      case 5: {
        monthName = "–∏—é–Ω—å";
        break;
      }
      case 6: {
        monthName = "–∏—é–ª—å";
        break;
      }
      case 7: {
        monthName = "–∞–≤–≥—É—Å—Ç";
        break;
      }
      case 8: {
        monthName = "—Å–µ–Ω—Ç—è–±—Ä—å";
        break;
      }
      case 9: {
        monthName = "–æ–∫—Ç—è–±—Ä—å";
        break;
      }
      case 10: {
        monthName = "–Ω–æ—è–±—Ä—å";
        break;
      }
      case 11: {
        monthName = "–¥–µ–∫–∞–±—Ä—å";
        break;
      }
    }

    switch (dayCount) {
      case 0: {
        dayName = "–≤–æ—Å–∫—Ä–µ—Å–µ–Ω—å–µ";
        break;
      }
      case 1: {
        dayName = "–ø–æ–Ω–µ–¥–µ–ª—å–Ω–∏–∫";
        break;
      }
      case 2: {
        dayName = "–≤—Ç–æ—Ä–Ω–∏–∫";
        break;
      }
      case 3: {
        dayName = "—Å—Ä–µ–¥–∞";
        break;
      }
      case 4: {
        dayName = "—á–µ—Ç–≤–µ—Ä–≥";
        break;
      }
      case 5: {
        dayName = "–ø—è—Ç–Ω–∏—Ü–∞";
        break;
      }
      case 6: {
        dayName = "—Å—É–±–±–æ—Ç–∞";
        break;
      }
    }

    return [dayName, dayNumber + " " + monthName];
  };
};
