import React from "react";
import styles from "./Forecast.module.css";
import DailyForecast from "./DailyForecast"
import Preloader from "../Common/Preloader/Preloader"
import Wind from "../../assets/images/wind.svg"
import Humidity from "../../assets/images/humidity.svg"
import Pressure from "../../assets/images/pressure.svg"

const Forecast = (props) => {

  if (!props.temp && !props.isLoad) {
    return <div className={styles.infoError}>–í–≤–µ–¥–∏—Ç–µ –≥–æ—Ä–æ–¥</div>
  }

  if(props.isError) {
    return <div className={styles.infoError}>–ü–æ –Ω–∞–π–¥–µ–Ω–Ω–æ–º—É –≥–æ—Ä–æ–¥—É –Ω–µ –¥–∞–Ω–Ω—ã—Ö –ø–æ –ø—Ä–æ–≥–Ω–æ–∑—É –ø–æ–≥–æ–¥—ã</div>
  }

  if (props.isLoad) {
    return <Preloader />
  }

  return (
    <div className={styles.forecast}>
      <div className={styles.cityName}>
        <span>{props.nameCity}</span>
      </div>
      <div className={styles.temp}>
        <div className={styles.currentTemp}>{props.temp > 0 && "+"}{props.temp}¬∞</div>
        <div className={styles.logo}>
          <img src={`http://openweathermap.org/img/wn/${props.iconId}@2x.png`} alt="" />
        </div>
        <div className={styles.info}>
          <p className={styles.infoForecast}>{props.descriptionWeather}<br/>–û—â—É—â–∞–µ—Ç—Å—è –∫–∞–∫ {props.feelsTemp > 0 && "+"}{props.feelsTemp}¬∞</p>
        </div>
      </div>
      <div className={styles.description}>
        <div><img className={styles.descriptionImages} src={Wind} /> –í–µ—Ç–µ—Ä {props.windSpeed} –º/—Å, {props.windDeg}</div>
        <div><img className={styles.descriptionImages} src={Humidity} /> –í–ª–∞–∂–Ω–æ—Å—Ç—å {props.humidity}%</div>
        <div><img className={styles.descriptionImages} src={Pressure} /> –î–∞–≤–ª–µ–Ω–∏–µ {props.pressure} –º–º —Ä—Ç. —Å—Ç.</div>
      </div>
      <div className={styles.dailyForecast}>
        {props.dailyForecast.map((dailyItem, index) => <DailyForecast key={index} dailyForecast={dailyItem}/>)}
      </div>
    </div>
  );
};

export default Forecast;
